  font-weight: 400;
  line-height: 1.5;
}

h1,
h2,
h3,
h4,
h5,
h6,
.header__title,
.menu__body ul li a {
  font-family: "Poppins", sans-serif;
  font-weight: 600;
}

h1 {
  font-size: 3.5rem;
}

h2 {
  font-size: 1.625rem;
  margin-bottom: 1rem;
}

h3 {
  font-size: 1.5rem;
}

h4 {
  font-size: 1.5rem;
}

h5 {
  font-size: 1.25rem;
}

h6 {
  font-size: 1rem;
}

strong {
  font-weight: 700;
}

a,
p {
  text-decoration: none;
  font-size: 1.125rem;
}

ul {
  margin: 0;
}

.section,
.container {
  padding-left: 20px;
  padding-right: 20px;
  max-width: 1140px;
  width: 100%;
  margin: 0 auto;
}

.d-flex {
  display: flex;
  align-items: center;
}

.d-grid {
