}

.d-mobile-only {
  display: block !important;
}
@media (min-width: 768px) {
  .d-mobile-only {
    display: none !important;
  }
}

.d-desktop-only {
  display: none !important;
}
@media (min-width: 768px) {
  .d-desktop-only {
    display: block !important;
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes slideIn {
  0% {
    transform: translateY(-100%);
  }
  100% {
    transform: translateY(0);
  }
}
@keyframes bounce {
  0%, 100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-15px);
  }
}
.curve-top {
  position: relative;
}
.curve-top::before {
  content: "";
  position: absolute;
  top: -10px;
  left: 0;
  width: 100%;
  height: 20px;
  background: none;
  background-image: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1200 120" preserveAspectRatio="none"><path d="M600,100 L0,0 L1200,0 Z" fill="%23fff"/></svg>');
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  z-index: 10;
}
@media (min-width: 768px) {
  .curve-top::before {
    height: 30px;
  }
}

.curve-bottom {
  position: relative;
}
.curve-bottom::after {
  content: "";
  position: absolute;
  bottom: -10px;
  left: 0;
  width: 100%;
  height: 20px;
  background: none;
  background-image: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1200 120" preserveAspectRatio="none"><path d="M600,0 L0,100 L1200,100 Z" fill="%23fff"/></svg>');
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  z-index: 10;
}
@media (min-width: 768px) {
  .curve-bottom::after {
    height: 30px;
  }
}